@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&family=Roboto:wght@300;400;700&display=swap');

/* Global Reset */
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #d44b3d;
  font-family: 'Roboto', sans-serif;
  overflow: hidden;
}

/* Main App Container */
.app-container {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

/* Snowflake */
.snowflake {
  position: absolute;
  background-color: white;
  border-radius: 50%;
  opacity: 0;
  animation: fall linear infinite, sway 3s ease-in-out infinite;
  animation-fill-mode: forwards;
}

/* Vertical Falling Animation */
@keyframes fall {
  0% {
    top: -100px;
    opacity: 0;
  }
  10% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  100% {
    top: 100vh;
    opacity: 1;
  }
}

/* Horizontal Swaying Animation */
@keyframes sway {
  0%, 100% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(10px);
  }
}

/* Festive Card Styling */
.card-content {
  position: relative;
  background: rgba(255, 255, 255, 0.9);
  border: 2px solid #e3d5c9;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  padding: 2rem 3rem;
  max-width: 600px;
  z-index: 10;
}

.card-content h1 {
  font-family: 'Great Vibes', cursive;
  font-size: 3rem;
  color: #d44b3d;
  margin-bottom: 1rem;
}

.card-content p {
  font-size: 1.2rem;
  color: #5a4d41;
  margin: 0.5rem 0;
}

.card-content p:last-child {
  margin-top: 1.5rem;
  font-weight: bold;
}

/* Rounded Image */
.rounded-image {
  width: 200px;
  height: auto;
  margin-bottom: 1rem;
  border-radius: 150px;  /* Slight rounding */
  box-shadow: 0 5px 10px rgba(0,0,0,0.1); /* Optional for a nice effect */
}
